import React, { Component } from 'react';

export default class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = { todo: this.props.todo };
    this.handleClick = this.handleClick.bind(this);
    this.handleTachar = this.handleTachar.bind(this);
    this.editarTodo = this.editarTodo.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleClick() {
    this.props.eliminarTodo(this.props.id);
  }

  handleTachar() {
    this.props.tacharTodo(this.props.id);
  }

  handleSubmit(evt) {
    evt.preventDefault();
    this.editarTodo(this.state.todo, this.props.id);
  }

  editarTodo() {
    this.props.editarTodo(this.state.todo, this.props.id);
  }

  render() {
    //console.log(this.props)
    const { hecho, editando , todo } = this.props;
    return (
      <div className='Todo'>
        {editando ? (
          <>
            <form type="submit" onSubmit={this.handleSubmit}>
              <input
                onChange={evt => this.setState({ todo: evt.target.value })}
                type='text'
                value={this.state.todo}
              />
              <button>Guardar</button>
            </form>
          </>
        ) : (
            <>
              <span
                onClick={this.handleTachar}
                className={hecho ? 'hecho' : null}>
                {todo}
              </span>
              <span>
                <button onClick={this.editarTodo}>
                  <i className='fa fa-edit'></i>
                </button>
                <button onClick={this.handleClick}>
                  <i className='fa fa-trash'></i>
                </button>
              </span>
            </>
          )}
      </div>
    );
  }
}
