import React, { Component } from 'react'
import uuid from 'react-uuid'

export default class TodoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newTodo: { id: '', todo: '', hecho: false, editando: false }
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(evt) {
    this.setState({
      newTodo: {
        id: uuid(),
        [evt.target.name]: evt.target.value,
        hecho: false,
        editando: false
      }
    });
  }

  handleSubmit(evt) {
    evt.preventDefault();
    (this.state.newTodo.todo && this.props.agregarTodo(this.state.newTodo))
    this.setState({ newTodo: { id: '', todo: '' } });
  }

  render() {
    return (
      <div className="Todo-Form" onSubmit={this.handleSubmit}>
        <form action="submit">
          <label htmlFor="todo">Nueva Tarea:</label><br />
          <input onChange={this.handleChange} type="text" name="todo" placeholder="Nueva Tarea..." value={this.state.newTodo.todo} />
          <button>Agregar</button>
        </form>
      </div>
    )
  }
}
