import React, { Component } from 'react'

import TodoForm from './TodoForm'
import Todo from './Todo'

export default class TodoList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: []
    }
    this.agregarTodo = this.agregarTodo.bind(this);
    this.eliminarTodo = this.eliminarTodo.bind(this);
    this.tacharTodo = this.tacharTodo.bind(this);
    this.editarTodo = this.editarTodo.bind(this);
    this.guardarEnLocalStorage = this.guardarEnLocalStorage.bind(this);
  }


  agregarTodo(todo) {
    this.setState({
      todos: [...this.state.todos, todo]
    }, this.guardarEnLocalStorage)
  }

  eliminarTodo(id) {
    const newState = this.state.todos.filter(todo => todo.id !== id);
    this.setState({ todos: newState }, this.guardarEnLocalStorage);
  }

  editarTodo(td, id) {
    const newState = this.state.todos.map(todo => {
      if (todo.id === id) {
        return { ...todo, todo: td, editando: !todo.editando }
      }
      return todo
    });
    this.setState({ todos: newState }, this.guardarEnLocalStorage)
  }

  tacharTodo(id) {
    const newState = this.state.todos.map(todo => {
      if (todo.id === id) {
        return { ...todo, hecho: !todo.hecho }
      }
      return todo
    });
    this.setState({ todos: newState }, this.guardarEnLocalStorage)
  }

  guardarEnLocalStorage() {
    localStorage.setItem("todo_state", JSON.stringify(this.state));
  }

  componentDidMount() {
    const state = JSON.parse(localStorage.getItem("todo_state")).todos;
    this.setState({ todos: state })
  }


  render() {
    const todos = this.state.todos.map(todo => <Todo key={todo.id} id={todo.id} todo={todo.todo} eliminarTodo={this.eliminarTodo} hecho={todo.hecho} editando={todo.editando} tacharTodo={this.tacharTodo} editarTodo={this.editarTodo} />)
    return (
      <div className="TodoList">
        <div className="TodoList-Header">
          <h2>Cosas Para Hacer</h2>
          <p>Una app simple hecha en React</p>
          <hr />
        </div>
        <div className="todos">
          {todos}
        </div>
        <TodoForm agregarTodo={this.agregarTodo} />
      </div>
    )
  }
}
